<template>
	<div>
		<el-row>
			<headers :title='title'></headers>
		</el-row>
		<el-row>
			<el-col :span='18' :offset='3'>
				<div class="news">
					<div class="news_title">
						<span>新闻编辑</span>
						<span class="r">
							<span class='rs'>*</span>
							<span>必填项</span>
						</span>
					</div>
					<el-row>
						<el-col :span='20' :offset='2'>
							<div class="news_form">
								<el-row>
									<el-col :span='12' >
										<el-form ref="form" :model="form" label-width="80px" :rules="rules">
  											<el-form-item label="新闻版块" class='is-required'>
  											  	<el-select v-model="form.section" placeholder="请选择" style='width:100%;'>
  										    <el-option
  										     	v-for="(item,index) in options"
  										      	:key="index"
  										      	:label="item.press_name"
  										      	:value="item.id">
  										    </el-option>
  										</el-select>
  											</el-form-item>
  											<el-form-item label="新闻标题" prop='title'>
  											  	<el-input v-model="form.title" maxlength="20"></el-input>
  											</el-form-item>
  											<el-form-item label="新闻图片" class='is-required'>
  												<div class="files">+
  													<div class="files_left">
  														<img :src="image" alt="" style='widows: 100px;height: 100px;' id="image">
  													</div>
  													<div class="files_right">
  														<input type="file" class="form-control" name='upload' @change="onFileChange">
  													</div>
  												</div>
  											</el-form-item>
  											<!-- <el-form-item label="版权作者" class='is-required'>
  												<el-input v-model="form.author"></el-input>
  											</el-form-item> -->
  										</el-form>
									</el-col>
								</el-row>
								<el-row>
									<el-col>
										<el-form label-width="80px">
											<el-form-item label="新闻正文" class='is-required'>
  												<div id="editor">
        											<p></p>
    											</div>
  											</el-form-item>
  											<el-form-item>
  												<el-button type="primary" id='btn2'>保存草稿</el-button>
  												<el-button type="primary" id='btn1'>发布</el-button>
  												<!-- <el-button type="primary">预览</el-button> -->
  											</el-form-item>
										</el-form>
									</el-col>
								</el-row>
							</div>
						</el-col>
					</el-row>
				</div>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import headers from '../../components/header.vue'
	import axios from 'axios'
	export default{
		components:{
			headers
		},
		data(){
			return{
				title:'新闻编辑',
				poster:'',
				image:'',/*图片*/
				options:'',
				form:{
					section:'',/*版块*/
					title:'',/*标题*/
					author:'',/*作者*/
					text:'',/*正文*/
					id:''
				},
				rules: {
					title: [{
						required: true,
						message: '标题不能为空'
					}, {
						min: 1,
						max: 25,
						message: '不能超过25字'
					}],
				}
			}
		},
		methods: {
			onFileChange(e) {
				var files = e.target.files || e.dataTransfer.files;
				if (!files.length)
					return;
				this.createImage(files[0]);

			},
			createImage(file) {
				var image = new Image();
				var reader = new FileReader();
				var vm = this;
				reader.onload = (e) => {
					vm.image = e.target.result;
				};
				reader.readAsDataURL(file);
			},
			/*保存*/
			save(){
				/*this.$confirm('是否保存?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then((action) => {
					if (action === 'confirm') {
						var formData = new window.FormData();
						var userId = window.sessionStorage.getItem('userId')
						formData.append('file', document.querySelector('input[type=file]').files[0])
						// formData.append('fileName',document.querySelector('input[type=file]').files[0].name)
						formData.append('title', this.form.title)
						formData.append('particulars', '详情')
						formData.append('content', '正文')
						formData.append('userId', userId)
						formData.append('pressTypeId', this.form.section)
						this.$axios.post("/tomlive/pressCenter/noIssueSelectPressCenter", formData, {
							headers: {
								"Content-Type": "multipart/form-data",
							}
						}).then((res) => {
							console.log(res)
							this.$router.push('/home/news_bc_c')
						})
					}
					}).catch(() => {
						this.$message({
							type: 'info',
							message: '已取消'
						});
					})*/
				var file;
				if(document.querySelector('input[type=file]').files[0]==undefined){
					file=''
					this.$confirm('是否发布?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then((action) => {
					if (action === 'confirm') {
						var formData = new window.FormData();
						var userId = window.sessionStorage.getItem('userId')
						formData.append('file',file)
						// formData.append('fileName',document.querySelector('input[type=file]').files[0].name)
						formData.append('title', this.form.title)
						formData.append('particulars', this.form.text)
						formData.append('userId', userId)
						formData.append('pressTypeId', this.form.section)
						formData.append('id',this.form.id)
						this.$axios.post("/tomlive/pressCenter/updateNoFile", formData, {
							headers: {
								"Content-Type": "multipart/form-data",
							}
						}).then((res) => {
							console.log(res)
							this.$axios.post('/tomlive/pressCenter/updatePressCenterStatus', this.$qs.stringify({
								id: this.form.id
							})).then((res) => {
								console.log(res)
								this.$router.push('/home/news_manage')
						})
							this.$router.push('/home/news_fb_c')
						})
					}
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消'
					});
				})
				}else{
					file=document.querySelector('input[type=file]').files[0]
					this.$confirm('是否发布?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then((action) => {
					if (action === 'confirm') {
						var formData = new window.FormData();
						var userId = window.sessionStorage.getItem('userId')
						formData.append('file',file)
						// formData.append('fileName',document.querySelector('input[type=file]').files[0].name)
						formData.append('title', this.form.title)
						formData.append('particulars', this.form.text)
						formData.append('userId', userId)
						formData.append('pressTypeId', this.form.section)
						formData.append('id',this.form.id)
						this.$axios.post("/tomlive/pressCenter/updateByPrimaryKeySelective", formData, {
							headers: {
								"Content-Type": "multipart/form-data",
							}
						}).then((res) => {
							this.$axios.post('/tomlive/pressCenter/updatePressCenterStatus', this.$qs.stringify({
								id: this.form.id
							})).then((res) => {
								console.log(res)
								this.$router.push('/home/news_manage')
						})
							console.log(res)
							this.$router.push('/home/news_fb_c')
						})
					}
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消'
					});
				})
				}
				console.log(file)
			},
			/*发布*/
			sub(){
				var file;
				if(document.querySelector('input[type=file]').files[0]==undefined){
					file=''
					this.$confirm('是否发布?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then((action) => {
					if (action === 'confirm') {
						var formData = new window.FormData();
						var userId = window.sessionStorage.getItem('userId')
						formData.append('file',file)
						// formData.append('fileName',document.querySelector('input[type=file]').files[0].name)
						formData.append('title', this.form.title)
						formData.append('particulars', this.form.text)
						formData.append('userId', userId)
						formData.append('pressTypeId', this.form.section)
						formData.append('id',this.form.id)
						this.$axios.post("/tomlive/pressCenter/updateNoFile", formData, {
							headers: {
								"Content-Type": "multipart/form-data",
							}
						}).then((res) => {
							console.log(res)
							this.$router.push('/home/news_fb_c')
						})
					}
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消'
					});
				})
				}else{
					file=document.querySelector('input[type=file]').files[0]
					this.$confirm('是否发布?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then((action) => {
					if (action === 'confirm') {
						var formData = new window.FormData();
						var userId = window.sessionStorage.getItem('userId')
						formData.append('file',file)
						// formData.append('fileName',document.querySelector('input[type=file]').files[0].name)
						formData.append('title', this.form.title)
						formData.append('particulars', this.form.text)
						formData.append('userId', userId)
						formData.append('pressTypeId', this.form.section)
						formData.append('id',this.form.id)
						this.$axios.post("/tomlive/pressCenter/updateByPrimaryKeySelective", formData, {
							headers: {
								"Content-Type": "multipart/form-data",
							}
						}).then((res) => {
							console.log(res)
							this.$router.push('/home/news_fb_c')
						})
					}
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消'
					});
				})
				}
				console.log(file)
			}
		},
		mounted(){
			var _this=this
			var arr2=window.sessionStorage.getItem('news_d');
			var arr=JSON.parse(arr2)
			this.form.id=arr.id
			console.log(arr.particulars)
			this.form.title=arr.title
			this.image=arr.photo
			var E = window.wangEditor;
			var editor = new E('#editor')
			editor.customConfig.uploadImgShowBase64 = true
            editor.customConfig.uploadImgMaxSize = 3 * 1024 * 1024
            editor.customConfig.uploadImgMaxLength = 5
			editor.create()
			/*var t=document.querySelector('.w-e-text-container>div')
			t.innerHTML=arr.particulars*/
			editor.txt.html(arr.particulars)
			document.getElementById('btn1').addEventListener('click', function () {
        		// 读取 html
        		_this.form.text=editor.txt.html()
				_this.sub()
    		}, false)
    		document.getElementById('btn2').addEventListener('click', function () {
        		// 读取 html
        		_this.form.text=editor.txt.html()
				_this.save()
    		}, false)
			/*版块类型*/
			this.$axios.post('/tomlive/pressType/selectAllPressType').then((res)=>{
				console.log(res)
				this.options=res.data.info
				this.form.section=arr.pressTypeId
			})

		}
	}
</script>
<style>
	.el-textarea .el-textarea__inner{
		resize: none;
	}
</style>
<style scoped>
	#editor{
		width: 100%;
		height: 400px;
	}
	.news{
		width: 100%;
		min-height: 500px;
		background: #fff;
		border: 1px solid #e9e9e9;
		box-sizing: border-box;
		margin: 20px 0;
	}
	.news_title{
		width: 100%;
		height: 50px;
		line-height: 50px;
		font-size: 14px;
		font-weight: bold;
		color: #666;
		border-bottom: 1px solid #e9e9e9;
	}
	.news_title>span{
		margin: 0 0 0 20px;
	}
	.news_form{
		width: 100%;
		min-height: 700px;
		padding: 80px 0 0 0;
	}
	.files{
		width: 100px;
		height: 100px;
		position: relative;
		border: 1px solid #e9e9e9;
		font-size: 66px;
		font-weight: 1;
		color: #e9e9e9;
		text-align: center;
		line-height: 100px;
	}
	.files>img{
		width: 100px;
		height: 100px;
	}
	.files_left{
		width: 100px;
		height: 100px;
		position: absolute;
		left: 0;
		top: 0;
	}
	.files_left>img{
		width: 100px;
		height: 100px;
	}
	.files_right{
		width: 100px;
		height: 100px;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 999;
	}
	.files_right>input{
		width: 100%;
		height: 100px;
		opacity: 0;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 999;
	}
	.news_title>.r{
		float: right;
		margin: 0 20px 0 0;
	}
	.r>.rs{
		color: red;
		margin: 0 5px;
	}
</style>
<style>
.el-message-box__wrapper{
	z-index: 999999999 !important;
}
	.el-message-box{
		z-index: 999;
	}
</style>